:root {
    --primaryColor: hsl(199, 100%, 33%);
    --SecondaryColor: hsl(187, 85%, 43%);
    --gradientColor: linear-gradient (to right, hsl(187, 85%, 43%), hsl(199, 100%, 33%));
    --whiteColor: hsl(0, 0%, 100%);
    --blackColor: hsl(201, 33%, 16%);
    --textColor: hsl(240, 4%, 36%);
    --whiteColorDeam: hsl(0, 0%, 93%);
    --greyText: rgb(190, 190, 190);
    --inputColor: rgb(239, 239, 239);
    --bodyColor: rgb(240, 240, 246);
    --cardBG: rgb(225, 225, 235);
    --cardHover: rgb(255, 255, 255);
}

.footer {
    width: 100%;
    position: relative;
    padding: 15px 0;
    display: flex;
    justify-content: center;
    margin: auto;
    overflow: hidden;
    z-index: 111;

    .videoDiv {
        position: absolute;
        height: 100%;
        width: 100%;

        video {
            height: 100%;
            object-fit: cover;
        }

        &::after {
            content: "";
            position: absolute;
            background: rgba(10, 84, 107, 0.308);
            top: 0;
            bottom: 0;
            right: 0;
            left: 0;
            mix-blend-mode: multiply;
        }
    }

    .secContent {
        width: 100%;
        height: max-content;
        padding: 1rem initial;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        margin: auto;
        row-gap: 2rem;
        z-index: 100;

        .contactDiv {
            justify-content: space-between;
            flex-direction: column;
            gap: 1rem;
            padding: 50px;

            .text {
                small {
                    font-size: 14px;
                    font-weight: 400;
                    color: var(--whiteColor);
                }
                h2 {
                    color: var(--whiteColor);
                    font-size: 1.5rem;
                }
            }

            .inputDiv {
                width: 100%;
                gap: 1rem;
                flex-direction: column;

                input {
                    width: 100%;
                    padding: 0.5rem 0.6rem;
                    border-radius: 22px;
                    border: 1px solid var(--whiteColor);
                    outline: none;
                    background: transparent;
                    color: var(--whiteColor);

                    &::placeholder {
                        color: white;
                        opacity: 0.5;
                    }
                }

                .btn {
                    width: 100%;
                    justify-content: center;
                    color: var(--whiteColor);
                    font-weight: 500;
                    gap: 0.5rem;

                    .icon {
                        font-size: 18px;
                    }
                }
            }
        }

        .footerCard {
            position: relative;
            padding: 1rem 1rem 4rem;
            gap: 2rem;
            border-radius: 20px;
            background: var(--bodyColor);
            width: 100%;
            justify-content: center;
            flex-direction: column;
            align-items: flex-start;
            overflow: hidden;

            .footerIntro {
                flex-basis: 50%;
                justify-content: center;
                align-items: flex-start;
                flex-direction: column;
                row-gap: 0.5rem;
            }

            .logoDiv {
                color: var(--blackColor);
                font-weight: 600;

                .icon {
                    font-size: 30px;
                    color: var(--primaryColor);
                    margin-right: 10px;
                }
            }

            .footerParagraph {
                font-size: 15px;
                color: var(--textColor);
            }

            .footerSocials {
                column-gap: 0.5rem;

                .icon {
                    color: var(--blackColor);
                    font-size: 20px;

                    &:hover {
                        color: var(--primaryColor);
                    }
                }
            }

            .footerLinks {
                flex-basis: 50%;
                width: 100%;
                gap: 5px;
                grid-template-columns: repeat(2, 1fr);
                justify-content: space-between;

                .linkGroup {
                    .groupTitle {
                        display: block;
                        font-weight: 600;
                        color: var(--blackColor);
                        margin-bottom: 0.5rem;
                    }

                    .footerLists {
                        font-size: 15px;
                        color: var(--textColor);
                        transition: 0.3s ease-in-out;

                        .icon {
                            font-size: 15px;
                            color: var(--greyText);
                        }
                        &:hover {
                            color: var(--primaryColor);
                            transform: translateX(10px);

                            .icon {
                                color: var(--SecondaryColor);
                            }
                        }
                    }
                }
            }

            .footerDiv {
                flex-direction: column;
                position: absolute;
                bottom: 0;
                left: 0;
                right: 0;
                background: var(--SecondaryColor);
                padding: 0.5rem 4rem;
                small {
                    font-size: 10px;
                    color: var(--whiteColor);
                }
            }
        }
    }
}


@media screen and (min-width:500px) {
    

        .footer{
            .secContent{
                .footerCard{
                    .footerLinks{
                        grid-template-columns: repeat(3,1fr);
                        padding-bottom : 1rem ;
                    }

                    .footerDiv{
                        justify-content: space-between;
                        flex-direction: row;


                        small{
                            font-size: 14px;
                            color: var(--whiteColor);
                        }
                    }
                }
            }
        }
}


@media screen and (min-width:600px) {

    .footer{
        .secContent{
            .contactDiv{
                flex-direction: row;
                justify-content: space-between;
           
                    .inputDiv{
                        width: max-content;
                        flex-direction: row;
                        justify-content: flex-start;

                        input{
                            width: 60%;
                        }

                        .btn{
                            width: max-content;
                        }
                    }
           
            }
        }
    }
}


@media screen and (min-width:840px) {

    .footer{
        padding: 5rem 0;
        .secContent{
    
            .footerCard{
                flex-direction: row;

                .footerLinks{
                    grid-template-columns: repeat(3,1fr);
                    padding-bottom:1rem;

                }
            }
        }
    }
}

@media screen and (min-width:1024px) {


    .footer{
        .secContent{
            .footerCard{
                padding: 3rem 1rem;

                .footerIntro{
                    .footerParagraph{
                        font-size: 15px;
                        color: var(--textColor);

                    }

                    .footerSocials{
                        column-gap: -5rem;

                        .icon{
                            color: var(--textColor);
                            font-size: 25px;
                            &:hover{
                                color: var(--primaryColor);
                                
                            }
                        }

                    }
                }
            .footerLinks{
                .linkGroup{
                    .footerLists{
                        font-size: 15px;
                        padding: 5px 0;
                    }
                }
            }
            
            }


        }
    }

}

